/**$*$********************************************************************
*$*$                         ELEMENTOS - TAG                             *
*$*$**********************************************************************/
body {
  font-family: "Fira Sans", sans-serif;
  background-color: #f3efef;
}

h1 {
  color: #464646;
}

h2 {
  color: #464646;
}

h3 {
  color: #525151;
}

h5 {
  color: #525151;
}

p {
  line-height: 1.5;
  font-size: 17px;
  font-weight: 200;
}

.separador {
  width: 100%;
  height: auto;
  margin-top: 40px;
}

.boxServicios {
  width: 50%;
  margin: 1%;
}

.cartCountwidth{
  width: 50%;
}

.nombreProductoCart{
  font-size: 1.2rem;
}

.stockPadding {
  padding-left: 60px;
}

.cartBoxContact{
  width: 40%;
  margin-left: 30%;
}

#basic-nav-dropdown{
  font-weight: bold;
  color: white;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-decoration: none;
}

