/*TABLET LANDSCAPE UP DISPOSITIVOS MEDIOS*/
@media screen and (max-width: 768px) {
  .mainGaleria {
    grid-template-columns: repeat(2, minmax(350px, 760px));
  }

  #parrafo2Founder,
  #parrafo3Founder {
    display: none;
  }

  #imgFounder {
    width: 100%;
    height: 500px;
  }

  .stockPadding {
    padding-left: 30px;
  }

  .cartBoxContact {
    width: 50%;
    margin-left: 30%;
  }

}

/*TELEFONOS - DISPOSITIVOS XS*/
@media screen and (max-width: 600px) {

  #imgFounder {
    margin-left: 20px;
  }

  #parrafo2Founder,
  #parrafo3Founder {
    display: block;
  }

  .flexServicios {
    flex-direction: column;
  }

  .flexCejas,
  .flexUnas {
    flex-direction: column-reverse;
  }

  .boxServicios {
    width: 100%;
    margin-bottom: 4%;
  }

}

/*TELEFONOS LANDSCAPE + TABLET VERTICAL DISPOSITIVOS SMALLS*/
@media screen and (max-width: 599px) {
  .mainGaleria {
    grid-template-columns: repeat(1, minmax(100px, 599px));
  }

  #imgFounder {
    width: 90%;
    height: auto;
  }

  .imgEmpleado {
    width: 80%;
    height: auto;
  }

  #colFounder {
    width: 90%;
    height: auto;
  }

  #colResenaFounder {
    width: 85%;
    height: auto;
  }

  #colResenaFounder h2 {
    text-align: center;
  }

  .logo__img--center {
    width: 80px;
    height: auto;
  }

  .title {
    top: 50%;
  }

  .menuSecciones {
    font-weight: normal;
    font-size: 14px;
    letter-spacing: 1px;
    margin-left: 30px;
  }

  .menuSecciones:hover {
    background-color: transparent;
  }

  .whatsappWidget {
    width: 50px;
    height: 50px;
    bottom: 20px;
    right: 20px;
    font-size: 33px;
    z-index: 100;
  }

  #idCarrito {
    width: 50px;
    height: 50px;
    bottom: 80px;
    right: 20px;
    font-size: 33px;
    z-index: 100;
  }

  #count {
    bottom: 125px;
    right: 40px;
    font-size: 15px;
  }

  .card-ancho {
    width: 100% !important;
  }

  .cartCountwidth {
    width: 100%;
  }

  .nombreProductoCart {
    font-size: 1rem;
  }

  .stockPadding{
    padding-left: 20px;
  }

  .cartBoxContact {
    width: 100%;
    margin-left: 0;
  }

  .cartBoxContact--button {
    width: 100%;
  }

}

/*TELEFONOS LANDSCAPE + TABLET VERTICAL DISPOSITIVOS SMALLS*/
@media screen and (max-width: 370px) {
  #imgFounder {
    width: 95%;
    height: auto;
  }

  #colFounder {
    width: 95%;
    height: auto;
  }

  #colResenaFounder {
    width: 90%;
    height: auto;
  }

  .imgEmpleado {
    width: 90%;
    height: auto;
  }
}
